@import "../../styleVariables.scss";

.chatUsers{
  // border:5px solid orange;
  width: 500px;
  border-radius:$border-radius;
  font-family: $font-family;
  color: $font-color;
  background-color: $component-bg-color;
  height: 75px; 
  transition: 0.5s ease-in-out;
  overflow-y: hidden;
  box-shadow: $box-shadow;



  

  .title{
    // border:3px solid green;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // font-size: 30px;
    cursor: pointer;


    .titleWrapper{
      display: flex;
      align-items: center;
      // border:3px solid green;
      margin: 0px 20px;



      .count{
        border-radius: 100px;
        background-color: #A5CEFF;
        margin-left: 20px;
        color: blue;
        display: flex;
        align-items: center;
        font-weight: 600;
        padding: 10px;
      }

    }

    &>h2{
      margin-left: 10px;
    }


    .icon{
      // border:3px solid green;
      margin-right: 20px;
      font-size: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.8s ease;
    }
  }
}




.activeIcon{
  transform: rotate(180deg);
  transition: 0.8s ease;
}


.activeChatUsers{
  transition: 0.5s ease-in-out;
  height: 945px;
  
}


.activeUsers{
  // border:9px solid red;
  overflow-y: auto;
  height: 850px;
  margin-top: 10px;

  

  .empty{
    display: flex;
    justify-content: center;
    align-items: center;
    // border:9px solid red;
    font-size: 30px;
    
  }


}




@media (max-width:1900px){

  .chatUsers{
    width: 90%;
    align-self: center;
    // height: 500px;
    margin: 30px auto 0 auto;


    .activeUsers{
      display: grid;
      grid-template-columns: 1fr 1fr;
      height: auto;
      
  
    }
  }

  .activeChatUsers{
    height: 450px;
    overflow-y: auto;
  }

}


@media (max-width:768px){
  
  .chatUsers{

    .activeUsers{
      grid-template-columns: 1fr;
    
      
  
    }
  }

  
}





