@import "../../styleVariables.scss";

.profileContent{
  // border:5px solid orange;
  // height: 100%;
  // width: 100%;
  display: grid;
  grid-template-areas: 
  'mainInfo info info'
  'mainInfo followedUsers followedUsers'
  'mainInfo followedUsers followedUsers';
  ;
  gap: 70px;
  // margin: 0px 100px;
  // padding: 0px 100px;
  grid-template-columns: 1fr 2fr;
  font-family: $font-family;
  color: $font-color;
  grid-template-rows: 1fr 1fr;
  padding: 0 50px;



  
  

  .info{
    grid-area: info;
    // border:5px solid blue;
  }
  

  .followedUsers{
    grid-area: followedUsers;
    // border:5px solid blue;
    background-color: $component-bg-color;
    border-radius: 20px;
    overflow: auto;



  }
}




@media (max-width:1763px){

  // 
  .profileContent{
    display: flex;
    flex-direction: column;
  }

  
  
}





@media (max-width:768px){
  
  .profileContent{
    display: flex;
    flex-direction: column;
  }
}

